@use 'base/variables' as v;
@use 'base/mixins' as m;

.modal{
  position: absolute;
  background-color: rgb( 0 0 0 /.8);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  .formulario{
    max-width: 60rem;
    background-color: v.$blanco;
    padding: 2rem;
    margin: 0 auto;
    opacity: 0;
    @include m.prefix(border-radius, 1rem);
    @include m.prefix(transform, translateY(-30rem));
    @include m.prefix(transition-property, all);
    @include m.prefix(transition-duration, .5s);
    @include m.prefix(transition-timing-function, cubic-bezier(0.455, 0.03, 0.515, 0.955));

    legend{
      font-size: 2rem;
      text-align: center;
      font-weight: bold;
      margin-bottom: 2rem;
      display: block;
    }

    input[type="text"]{
      border: 1px solid v.$gris;
      @include m.prefix(border-radius, .5rem);
    }

    .opciones{
      display: flex;
      justify-content: space-between;
    }
    .submit-nueva-tarea{
      @include m.boton(v.$indigo, v.$blanco);
    }

    .cerrar-modal{
      @include m.boton(v.$naranja, v.$blanco);
    }

    &.animar{
      @include m.vertical-center();
      opacity: 1;
    }

    &.cerrar{
      @include m.prefix(transform, translateY(-200%));
      opacity: 0;
    }
  }
}