@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenedor-nueva-tarea{
  display: flex;
  justify-content: center;

  .agregar-tarea{
    @include m.boton(v.$cyan, v.$blanco);
  }
}

.listado-tareas{
  list-style: none;
  padding: 0;
  margin-top: 3rem;

  .no-tareas{
    text-align: center;
  }
}

.tarea{
  padding: 1rem;
  border-bottom: 2px solid darken(v.$grisClaro, 5%);
  margin-bottom: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  
  @include m.tablet{ 
    flex-direction: row;
  }

  &:last-of-type{
    border: none;
  }

  p{
    font-size: 2rem;

    &:hover{
      cursor: pointer;
    }
  }

  .opciones{
    display: flex;
    gap: 1.5rem;
    width: 100%;
    justify-content: space-around;

    @include m.tablet{ 
      width: auto;
      gap: 2rem;
    }
    
    button{
      border: none;
      font-size: 1.5rem;
      padding: 1rem;
      text-transform: uppercase;
      font-weight: 700;
      @include m.prefix(border-radius, 1rem);

      &:hover{
        cursor: pointer;
      }
    }

    .completa{
      background-color: v.$azul;
      color: v.$blanco;
    }

    .pendiente{
      background-color: v.$naranja;
      color: v.$blanco;
    }

    .eliminar-tarea{
      background-color: v.$rojo;
      color: v.$blanco;
    }
  }
}