@use 'base/variables' as v;
@use 'base/mixins' as m;

.sidebar{
  opacity: 0;
  background-color: v.$indigo;
  height: 0;
  overflow: hidden;
  @include m.prefix(transition-property, opacity);
  @include m.prefix(transition-duration, 0.5s);

  &.mostrar{
    padding: 2rem;
    opacity: 1;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: transparentize(v.$indigo, 0.05);

    @include m.tablet{ 
      display: none;
    }
  }

  &.ocultar{
    opacity: 0;
  }

  .contenedor-sidebar{
    display: flex;
    justify-content: space-between;
    align-items: center;

    h2{
      color: v.$blanco;
      margin: 0;
      text-align: center;
    }

    .cerrar-menu{
      display: flex;
      justify-content: center;
      height: 3rem;
      width: 3rem;
      @include m.tablet{ 
        display: none;
      }

      img{
        &:hover{
          cursor: pointer;
        }
      }
    }
  }

  @include m.tablet{ 
    height: 100%; 
    opacity: 1;
    padding: 2rem 3rem;
    background-color: v.$indigo;
  }

  .sidebar-nav{
    margin-top: 5rem;

    a{
      color: v.$blanco;
      display: block;
      padding: .7rem;
      @include m.prefix(transition-property, background-color);
      @include m.prefix(transition-duration, .3s);
      font-weight: bold;
      font-size: 2.4rem;
      margin-bottom: 2rem;
      text-align: center;

      &:hover,
      &.activo{
        background-color: darken(v.$indigo, 20%);
        @include m.prefix(border-radius, .5rem);
      }

      @include m.tablet{ 
        font-size: 2rem;
        margin-bottom: 1.5rem;
        text-align: left;
      }
    }
  }

  .cerrar-sesion-mobile{
    margin-top: 10rem;

    .cerrar-sesion{
      @include m.boton(v.$cyan, v.$blanco);
    }

    @include m.tablet{ 
      display: none;
    }
  }
}