@use 'base/variables' as v;
@use 'base/mixins' as m;

.no-proyectos{

  a{
    color: v.$indigo;
  }
}

.listado-proyectos{
  list-style: none;
  padding: 0;
  margin-top: 3rem;
  @include m.grid(1, 2rem);

  @include m.tablet{
    @include m.grid(2, 2rem);
  }

  @include m.desktop{
    @include m.grid(4, 2rem);
  }

  .proyecto{
    background-color: lighten(v.$indigo, 5%);
    min-height: 12rem;
    display: flex;
    align-items: center;
    padding-left: 2rem;
    line-height: 2;
    border-radius: .5rem;
    -webkit-border-radius: .5rem;
    -moz-border-radius: .5rem;
    -ms-border-radius: .5rem;
    -o-border-radius: .5rem;
    
    &:hover{
      background-color: darken(v.$indigo, 5%);
    }
    
    a{
      color: v.$blanco;
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
    }
  }
}
